      // –ï—Å–ª–∏ –Ω–µ—Ç –≤—ã–±—Ä–∞–Ω–Ω—ã—Ö –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–≤, —Å–±—Ä–∞—Å—ã–≤–∞–µ–º –Ω–∞ –±–∞–∑–æ–≤—ã–π —Ç–æ–≤–∞—Ä
